<template>
    <div class="container">
        <div class="board">
            <div v-bind:key="row" v-for="row in renderBoard" class="board-row">
                <div v-bind:key="cell" :style="{'height': cellSize, 'width': cellSize}" v-for="cell in row"
                     class="board-cell">
                    <div class="chess-piece" :style="{'font-size': cellSize}">{{ cell.icon }}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {initGame} from "./gameModes/default";

    export default {
        name: "Chess",
        methods: {},
        beforeCreate() {
        },
        data() {
            return {
                game: initGame(),
            }
        },
        computed: {
            renderBoard() {
                const board = [];
                for (let i = 0; i < this.game.board.scaleY; i++) {
                    const row = [];
                    for (let j = 0; j < this.game.board.scaleX; j++) {
                        const cell = {};
                        const piece = this.game.board.chessPieces.find(p => {
                            return p.posX === j && p.posY === i;
                        });
                        if (piece) {
                            cell.icon = piece.iconWhite;
                            if (piece.color === 'black') {
                                cell.icon = piece.iconBlack;
                            }
                        }
                        row.push(cell);
                    }
                    board.push(row)
                }
                // eslint-disable-next-line no-debugger
                return board;
            },
            cellSize() {
                const boardSize = Math.max(3, this.game.board.scaleY, this.game.board.scaleX);
                if (window.innerHeight > window.innerWidth) {
                    return Math.floor(100 / boardSize * 0.9) + 'vw';
                } else {
                    return Math.floor(100 / boardSize * 0.9) + 'vh';
                }
            }
        }
    }

</script>

<style scoped>
    .board-row {
        display: flex;
        width: auto;
        clear: both;
    }

    .board-cell {
        float: left;
    }

    .board .board-row:nth-child(even) .board-cell:nth-child(odd),
    .board .board-row:nth-child(odd) .board-cell:nth-child(even) {
        background: #d18b47;
    }

    .board .board-row:nth-child(odd) .board-cell:nth-child(odd),
    .board .board-row:nth-child(even) .board-cell:nth-child(even) {
        background: #ffce9e;
    }

    .board {
        display: flex;
        flex-direction: column;
    }

    .chess-piece {
        font-family: stchess, serif;
    }

    .container {
        width: 100%;
        display: flex;
        justify-content: center;
        height: 100vh;
        align-items: center;
    }

</style>